export default {
  namespaced: true,
  state: {
    context: null,
    img: null,
    imgLoading: false,
    scale: null,
    resized: false,
    height: 0,
    width: 0
  },
  mutations: {
    setImage (state, data) {
      state.img = data
    },
    setImageLoading (state, data) {
      state.imgLoading = data
    },
    setScale (state, data) {
      state.scale = data
    },
    setContext (state, data) {
      state.context = data
    },
    setSize (state, size) {
      state.height = size.h
      state.width = size.w
      state.resized = true
    },
    scaleUp (state) {
      state.scale += 0.1
    },
    scaleDown (state) {
      state.scale -= 0.1
    }
  },
  getters: {
    image: (state) => state.img,
    imageLoading: state => state.imgLoading,
    scale: state => state.scale,
    context: (state) => state.context,
    size: (state) => {
      return { h: state.height, w: state.width }
    },
    resized: (state) => {
      return state.resized
    },
    canvasRatio: (state) => {
      return { x: state.width / 256, y: state.height / 256 }
    }
  },
  actions: {
    setImage ({ commit }, data) {
      commit('setImage', data)
    },
    setImageLoading ({ commit }, data) {
      commit('setImageLoading', data)
    },
    setScale ({ commit }, data) {
      commit('setScale', data)
    },
    setContext ({ commit }, data) {
      commit('setContext', data)
    },
    scaleUp ({ commit }) {
      commit('scaleUp')
    },
    scaleDown ({ commit }) {
      commit('scaleDown')
    },
    async setSize ({ commit, getters, dispatch }, data) {
      if (data.x === 0) return
      commit('setSize', data)
      await dispatch('results/resize', getters.canvasRatio, { root: true })
      console.log('resize done')
    }
  }
}
