export default {
  namespaced: true,
  state: {
    mode: 'display',
    dragging: false,
    cursor: 'default',
    startX: null,
    startY: null
  },
  mutations: {
    setMode (state, data) {
      state.mode = data
    },
    setDragging (state, data) {
      state.dragging = data
    },
    setStartPosition (state, data) {
      state.startX = data.x
      state.startY = data.y
    },
    setCursor (state, data) {
      state.cursor = data
    }
  },
  getters: {
    mode: (state) => {
      return state.mode
    },
    dragging: (state) => {
      return state.dragging
    },
    cursor: (state) => {
      return state.cursor
    },
    startX: (state) => {
      return state.startX
    },
    startY: (state) => {
      return state.startY
    }
  },
  actions: {
    setMode ({ commit }, data) {
      commit('setMode', data)
    },
    setDragging ({ commit }, data) {
      commit('setDragging', data)
    },
    setCursor ({ commit }, data) {
      commit('setCursor', data)
    },
    setStartPosition ({ commit }, data) {
      commit('setStartPosition', data)
    }
  }
}
